// 操作日志相关接口
import axios from './../fetch'

// 查询平台日志
export const logPlatList = params => { return axios.get('/admin/platform/platformLog/page', { params: params }) }

// 删除平台日志
export const logPlatDelete = params => { return axios.get('/admin/platform/platformLog/delete', { params: params }) }

// 查询商家日志
export const logMerchantList = params => { return axios.get('/admin/platform/storeLog/page', { params: params }) }

// 删除商家日志
export const logMerchantDelete = params => { return axios.get('/admin/platform/storeLog/delete', { params: params }) }



