<template>
  <div class="cms-list-layout" v-loading="loading" >
    <cms-tabs>
      <cms-tab to="/log/paltLog">平台操作日志</cms-tab>
      <cms-tab to="/log/storeLog">商家操作日志</cms-tab>
    </cms-tabs>
    <cms-prompt collapse='true'>
      <li>显示商家所有的操作日志</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>操作用户</label>
        <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>IP</label>
        <el-input v-model="queryParams.clientIp" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>商家</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
      <div class="query-group">
        <label>请选择清除时间段</label>
        <el-select v-model.number="queryParams.type" >
          <el-option label="3个月前" :value="1"></el-option>
          <el-option label="半年前" :value="2"></el-option>
          <el-option label="一年前" :value="3"></el-option>
        </el-select>
        <cms-button v-prem="'/paltLog/delete'" style="margin-left:10px;"
        icon="error" type="error" size="small" @click="deleteDatas()">删除
        </cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column prop="uri" label="请求地址" min-width="260"></el-table-column>
      <el-table-column prop="clientIp" label="访问/请求IP" min-width="150"></el-table-column>
      <el-table-column prop="method" label="请求方式" min-width="140"></el-table-column>
      <el-table-column prop="store.storeName" label="商家" min-width="150">
      </el-table-column>
      <el-table-column prop="username" label="操作用户" min-width="150"></el-table-column>
      <el-table-column prop="createTime" label="请求时间" min-width="170">
          <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { logMerchantList, logMerchantDelete } from '@/api/setting/log.js'
var defaultData = {
  'username': '',
  'clientIp': '',
  'storeName': ''
}
export default {
  name: 'storeLog',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteDatas() {
      if (this.queryParams.type) {
        this.deleteTableType()
      } else {
        this.$errorMessage('请先选择时间段')
      }
    },
    deleteTableType() {
      // 根据类型删除
      if (this.type !== 0) {
        this.$confirm('您确定要批量删除数据吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            this.loading = true
            logMerchantDelete({ type: parseInt(this.queryParams.type) }).then(res => {
              if (res.code === 200) {
                this.getTableData(this.callback, this.queryParams, this.isPage)
                this.$successMessage('删除成功')
                this.loading = false
              } else {
                this.$errorMessage('删除失败,' + res.message)
                this.loading = false
              }
            })
          })
          .catch(() => {})
      } else {
        this.$errorMessage('请选择类型!')
      }
    }
  },
  created() {
    // 首次触发
    this.getTableData(logMerchantList, this.queryParams)
  }
}
</script>

<style>
</style>



